// IMPORTS
// Material UI
import IconButton from '@material-ui/core/IconButton'
import Paper from '@material-ui/core/Paper'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'
import PencilIcon from '@material-ui/icons/Edit'
import DeleteIcon from '@material-ui/icons/Delete'
import ListIcon from '@material-ui/icons/List'
import PropTypes from 'prop-types'

// Externals imports (Dependencies)
import React, { Component } from 'react'
import { Link } from 'react-router-dom'

// Internal imports
import Paginate from '../../main/pages/Paginate'
import { fetchStores } from '../StoreActions'
import consts from '../../../consts'

class List extends Component {
  
  constructor(props){
    super(props)

    this.state = {
        rowsPerPage: consts.DEFAULT_PAGINATION,
    }
  }

  handleChangeRowsPerPage = event => {
    this.setState({rowsPerPage: event.target.value}, () => {
      this.onChangePage(null, null, 0);
    })
  }

  onChangePage = (event, newPage) => {
    this.props.fetchStores(newPage + 1,
      this.props.filters,
      this.state.rowsPerPage)
    .then(data => {})
  }

  render() {
    const { classes, items } = this.props
    return (
      <Paper className={classes.content} elevation={1}>
        <Table>
          <TableHead>
            <TableRow>
              <TableCell>Identificador</TableCell>
              <TableCell>Descrição</TableCell>
              <TableCell>CNPJ</TableCell>
              <TableCell>Frete Mínimo</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {items && items.data && items.data.map(item => (
              <TableRow key={item.id}>
                <TableCell>{item.id}</TableCell>
                <TableCell>{item.description}</TableCell>
                <TableCell>{item.CNPJ}</TableCell>
                <TableCell>{item.min_freight}</TableCell>
                <TableCell>
                  <IconButton
                    size="small"
                    component={Link}
                    to={`/stores/${item.id}/edit`}
                  >
                    <PencilIcon />
                  </IconButton>
                  
                  <IconButton
                    size="small"
                    component={Link}
                    to={`/stores/${item.id}/delete`}
                  >
                    <DeleteIcon />
                  </IconButton>

                  <IconButton
                    size="small"
                    component={Link}
                    to={`/stores/${item.id}/list-centers`}
                  >
                    <ListIcon />
                  </IconButton>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
        <Paginate
          handleChangeRowsPerPage={this.handleChangeRowsPerPage}
          rowsPerPage={this.state.rowsPerPage}
          rows={items.total}
          page={items.current_page -1}
          handleChangePage={this.onChangePage} />
      </Paper>
    )
  }
}

List.propTypes = {
  classes: PropTypes.object.isRequired,
  items: PropTypes.array.isRequired,
}

List.defaultProps = {
  items: [],
}

export default List
